.layout_ef {
  height: 100%;
  background: #FFFFFF;
  padding: 12px 0px 0px 20px;
}
.layout_ef .ef .title {
  font-family: MicrosoftYaHei;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
}
.layout_ef .ef .cardlist {
  padding: 8px 0px;
  display: flex;
  flex-wrap: wrap;
}
.layout_ef .ef .cardlist .card {
  width: 300px;
  height: 120px;
  background: rgba(85, 130, 243, 0.12);
  border-radius: 4px;
  padding: 12px;
  margin-right: 16px;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  margin-bottom: 12px;
}
.layout_ef .ef .cardlist .card .card_coin {
  position: absolute;
  top: 15px;
  left: 0;
  background: var(--theme-color);
  border-radius: 2px;
  width: 4px;
  height: 12px;
}
.layout_ef .ef .cardlist .card .card_title {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  line-height: 18px;
  height: 18px;
  font-weight: 600;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.layout_ef .ef .cardlist .card .card_info {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #999999;
  letter-spacing: 0;
  font-weight: 400;
  display: flex;
  margin-top: 12px;
}
.layout_ef .ef .cardlist .card .card_info .card_info_link {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: var(--theme-color);
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  text-decoration: none;
  margin-left: 4px;
}
.layout_ef .ef .cardlist .card .card_info_kg {
  margin-left: 40px;
}
.layout_ef .ef .cardlist .card .card_description {
  height: 30px;
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #666666;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  margin-top: 12px;
}
.layout_ef .ef .cardlist .card:hover {
  border: 1px solid var(--theme-color);
}
.layout_ef .ef .cardlist .card:hover .card_title {
  color: var(--theme-color);
}
